import { useState,useRef } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import PropTypes from 'prop-types';
import { formatDistanceToNowStrict } from 'date-fns';
import numeral from 'numeral';
import PencilAlt from "../../../icons/PencilAlt";
import XIcon from "../../../icons/X";
import { alpha } from '@material-ui/core/styles';
import Amplify, { Auth } from 'aws-amplify';
import  {Storage} from "aws-amplify";
import WarningIcon from '@material-ui/icons/WarningOutlined';
import * as Yup from "yup";
import { Formik, setNestedObjectValues } from "formik";
import PlusIcon from '../../../icons/Plus';
import MobileDatePicker from '@material-ui/lab/MobileDatePicker';
import {
  Avatar,
  Box,
  Popover,
  TextField,
  Dialog,
  Chip,
  FormHelperText,
  Card,
  CardMedia,
  Divider,
  Grid,
  IconButton,
  Link,
  Rating,
  Tooltip,
  Typography,
  Button
} from '@material-ui/core';
import toast from "react-hot-toast";
import red from '@material-ui/core/colors/red';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import UsersIcon from '../../../icons/Users';
import getInitials from '../../../utils/getInitials';
import SendIcon from '@material-ui/icons/Send';
import { projectsApi} from "../../../api/projects";
import useAuth from '../../../hooks/useAuth';
const ProjectCard = (props) => {
  const { handlereRender,project, ...other } = props;
  console.log("pr",project.type)
  const [selectedValue, setSelectedValue] = useState([1]);
  const [isLiked, setIsLiked] = useState(project.isLiked);
  const [likes, setLikes] = useState(project.likes);
  const [open, setOpen] = useState(false);
  const [open2, setOpen2] = useState(false);
  const anchorRef = useRef(null);
    const [avatar, setAvatar ]= useState('');
  const { user } = useAuth();
  const [tag, setTag] = useState('');
   const [User, setUser] = useState('');
  const handleLike = () => {
    setIsLiked(true);
    setLikes((prevLikes) => prevLikes + 1);
  };
const handleClose = () => {
    setOpen(false);
  };
    const getAvatar = async (fileName) => {
    await Storage.get(fileName,{
    level: 'public'}).then(avatar => {
       setAvatar(avatar)
    }
    )
  } ;

  

    const updateProject = async () => {
    const projectt = {
      //userId: project.userId,
      userId:project.userId,
      id: project.id,
      name: project.name,
      tags: project.tags,
      projectPhone: project.projectPhone,
      startDate: project.startDate,
      type: project.type,
      endDate: project.endDate,
      description: project.description
  
   
    };
    toast.success("Project Updated");
                handleClose();

    return await projectsApi.updateProject(projectt);
  };

  const deleteProject = async () => {
    const projectt = {
      id: project.id,
    };
     await projectsApi.deleteProject(projectt)
     .then(toast.success("Successfully deleted"),
     handleClose2(),
     handlereRender())
    .catch(err => {
     toast.error('Unexpected error while deleting, try again', err);
     
    });

  };

   Auth.currentAuthenticatedUser().then(user =>  {

       if (user.attributes.picture){
       getAvatar(user.attributes.picture) }
     });
  const handleClose2 = () => {
    setOpen2(false);
    handlereRender()
    ;
  };

  const handleUnlike = () => {
    setIsLiked(false);
    setLikes((prevLikes) => prevLikes - 1);
  };

  return (
    <Card {...other}>
      <Box sx={{ p: 3 ,flexGrow: 1}}>
        {project.name}

        <Box
        sx={{
          alignItems: 'center',
          display: 'flex',
      
        }}
      >

   
         </Box>

         <Popover
          anchorEl={anchorRef.current}
          anchorOrigin={{
            horizontal: "left",
            vertical: "bottom",
          }}
          onClose={handleClose}
          open={open}
        >
             <Formik
            initialValues={{
              name: project.name,
              tags: project.tags,
              projectPhone: project.projectPhone,
              startDate: project.startDate,
              type: project.type,
              endDate: project.endDate,
              Description: project.description,
              submit: null,
            }}
            validationSchema={Yup.object().shape({
              name: Yup.string().required(
                "Project Name is a required field"
              ),
              tags: Yup.string(),
              Location: Yup.string().required(),
              projectPhone: Yup.number().required(
                "Number to call is a required field"
              ),
              startDate: Yup.date().required("Date is required"),
             type: Yup.string(),
              Description: Yup.string(),
              endDate: Yup.date().required("Date is required"),
            })}
            onSubmit={async (
              values,
              { setErrors, setStatus, setSubmitting }
            ) => {
              try {
                // Call API to store step data in server session
                // It is important to have it on server to be able to reuse it if user
                // decides to continue later.

                updateProject();
                toast.success("Project Updated");
                handleClose();

                // window.location.reload();
                setStatus({ success: true });
                setSubmitting(false);

             
              } catch (err) {
                console.error(err);
                setStatus({ success: false });
                setErrors({ submit: err.message });
                setSubmitting(false);
              }
            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              setFieldValue,
              setFieldTouched,
              touched,
              values,
            }) => (
              <form onSubmit={handleSubmit} {...other}>
                <Card sx={{ p: 3 }}>
                  <Typography color="textPrimary" variant="h6">
                    Project details
                  </Typography>

                  <Box sx={{ mt: 2 }} />
                  <TextField
                    error={Boolean(touched.name && errors.name)}
                    fullWidth
                    helperText={touched.name && errors.name}
                    label={project.name}
                    name="Project Name"
                    onBlur={handleBlur}
                    onChange={(e) => {
                      values.name = e.target.value;
                      setFieldValue("Project Name", e.target.value);
                      project.name = e.target.value;
                    }}
                    variant="outlined"
                    value={values.name}
                  />

                  <Box sx={{ mt: 2 }} />
                  <TextField
                    error={Boolean(touched.Description && errors.Description)}
                    fullWidth
                    helperText={touched.Description && errors.Description}
                    label="Description"
                    name="Description"
                    onBlur={handleBlur}
                    onChange={(e) => {
                      project.description = e.target.value;
                      values.Description = e.target.value;
                      setFieldValue("Description", e.target.value);
                    }}
                    value={values.Description}
                    variant="outlined"
                  />

                  <Box
                    sx={{
                      alignItems: "center",
                      display: "flex",
                      mt: 2,
                    }}
                  ></Box>
                  <Box sx={{ mt: 2 }} />

                

                  <Box sx={{ mt: 2 }} />
                  <TextField
                    error={Boolean(
                      touched.tags && errors.tags
                    )}
                    fullWidth
                    helperText={
                      touched.tags && errors.tags
                    }
                    label="Tags"
                    name="Tags"
                    onBlur={handleBlur}
                    onChange={(e) => {
                      project.tags = e.target.value;
                      values.tags = e.target.value;
                      setFieldValue("Tags", e.target.value);
                    }}
                    value={values.tags}
                    variant="outlined"
                  />
                  <Box sx={{ mt: 2 }} />

              

                  <Box sx={{ mt: 2 }} />

                  <TextField
                    error={Boolean(
                      touched.projectPhone && errors.projectPhone
                    )}
                    fullWidth
                    helperText={touched.projectPhone && errors.projectPhone}
                    label="Number To Call"
                    name="Number To Call"
                    type="number"
                    onBlur={handleBlur}
                    onChange={(e) => {
                      project.projectPhone = e.target.value;
                      values.projectPhone = e.target.value;
                      setFieldValue("projectPhone", e.target.value);
                    }}
                    variant="outlined"
                    value={values.projectPhone}
                 
                  />

                  <Box sx={{ mt: 2 }}>
                    <MobileDatePicker
                      label="Start Date"
                      onAccept={(date) => {
                        project.startDate = date.toLocaleDateString();
                        values.startDate = date;
                        setFieldTouched("=startDate");
                      }}
                      onChange={(date) =>
                        setFieldTouched("=startDate", date)
                      }
                      onClose={() => setFieldTouched("=startDate")}
                      renderInput={(inputProps) => (
                        <TextField variant="outlined" {...inputProps} />
                      )}
                      name="startDate"
                      value={values.startDate}
                    />
                    {Boolean(touched.startDate && errors.startDate) && (
                      <FormHelperText error>
                        {errors.startDate}
                      </FormHelperText>
                    )}
                  </Box>

                    <Box sx={{ mt: 2 }}>
                    <MobileDatePicker
                      label="End Date"
                      onAccept={(date) => {
                        project.endDate = date.toLocaleDateString();
                        values.endDate = date;
                        setFieldTouched("=endDate");
                      }}
                      onChange={(date) =>
                        setFieldTouched("=endDate", date)
                      }
                      onClose={() => setFieldTouched("=endDate")}
                      renderInput={(inputProps) => (
                        <TextField variant="outlined" {...inputProps} />
                      )}
                      name="endDate"
                      value={values.endDate}
                    />
                    {Boolean(touched.endDate && errors.endDate) && (
                      <FormHelperText error>
                        {errors.endDate}
                      </FormHelperText>
                    )}
                  </Box>

                  <Box sx={{ mt: 2 }}>
                   
                  </Box>
                  <Box
                    sx={{
                      display: "flex",
                      mt: 6,
                    }}
                  >
                    <Box sx={{ m: 1, justifyContent: "center" }}>
                      <Button
                        color="primary"
                        disabled={isSubmitting}
                        type="submit"
                        variant="contained"
                        onClick={updateProject}
                      >
                        Update
                      </Button>
                    </Box>
                    <Box sx={{ m: 1 }}>
                      <Button
                        color="primary"
                        onClick={handleClose}
                        variant="outlined"
                      >
                        Cancel
                      </Button>
                    </Box>
                  </Box>
                </Card>
              </form>
            )}
          </Formik>
        </Popover>


        <Box
          sx={{
            alignItems: 'center',
            display: 'flex',
            mt: 2
          }}
        >
          <Avatar
            alt="Author"
            src={avatar}
          >
            {getInitials(user.name)}
          </Avatar>
          <Box sx={{ ml: 2 }}>
            <Link
              color="textPrimary"
              component={RouterLink}
              to="#"
              variant="h6"
            >
              {project.title}
            </Link>
            <Typography
              color="textSecondary"
              variant="body2"
            >
              by
              {' '}
              <Link
                color="textPrimary"
                component={RouterLink}
                to="#"
                variant="subtitle2"
              >
                {user.name}
              </Link>
            
            </Typography>
          </Box>
        </Box>
      </Box>
      <Box
        sx={{
          pb: 2,
          px: 3
        }}
      >
        <Typography
          color="textSecondary"
          variant="body2"
        >
          {project.caption}
        </Typography>
      </Box>
      <Box
        sx={{
          px: 3,
          py: 2
        }}
      >
        <Grid
          alignItems="center"
          container
          justifyContent="space-between"
          spacing={3}
        >
          <Grid item>
            <Typography
              color="textPrimary"
              variant="subtitle2"
            >
              {project.projectPhone}
             
            </Typography>
            <Typography
              color="textSecondary"
              variant="body2"
            >
              Phone Number
            </Typography>
          </Grid>
          <Grid item>
               <Box
          sx={{
            alignItems: 'center',
            display: 'flex',
         
          }}
        >
             <Button
                  color="primary"
                  component={RouterLink}
                  startIcon={<SendIcon fontSize="small" />}
                   sx={{display: 'flex',mt:-30,}}
                  to={"/dashboard/projects/" + project.id}
                  variant="contained"
                >
                  Details
        </Button>
        </Box>
            <Typography
              color="textPrimary"
              variant="subtitle2"
            >
              {project.type}
            </Typography>
            <Typography
              color="textSecondary"
              variant="body2"
            >
              Type
            </Typography>
          </Grid>
        </Grid>
      </Box>
      <Divider />
      <Box
        sx={{
          alignItems: 'center',
          display: 'flex',
          pl: 2,
          pr: 3,
          py: 2
        }}
      >

        <Box
          sx={{
            alignItems: 'center',
            display: 'flex',
            ml: 2
          }}
        >
         <UsersIcon fontSize="small" />
          <Typography
            color="textSecondary"
            sx={{ ml: 1 }}
            variant="subtitle2"
          >
            {project.userId.length}
          </Typography>
        </Box>
        <Box sx={{ flexGrow: 1 }} />
   
          <Button
         color="primary"
                  component={RouterLink}
                     startIcon={<PencilAlt fontSize="small" />}
                  sx={{ m: 1 }}
                  to={"/dashboard/projects/update" + project.id}
                  variant="contained"
        >
          Edit 
        </Button>
        <Button
        sx={{ color: 'red'}}
        startIcon={  <XIcon  fontSize="small" sx={{ color: 'red'}} />}
        onClick={() => {
                              
          setOpen2(true);
        }}
        
        >
       
          Delete 
        </Button>
         <Dialog selectedValue={selectedValue}
                            open={open2}
                            onClose={handleClose2}
                            BackdropProps={{ style: { backgroundColor: "transparent" } 
                            }}>

        <Box
    sx={{
      backgroundColor: 'background.default',
      minHeight: '100%',
      p: 3
    }}
  >
    
     
        <Box
          sx={{
            display: 'flex',
            pb: 2,
            pt: 3,
            px: 3
          }}
        >
          <Avatar
            sx={{
              backgroundColor: (theme) => alpha(theme.palette.error.main, 0.08),
              color: 'error.main',
              mr: 2
            }}
          >
            <WarningIcon />
          </Avatar>
          <Box>
            <Typography
              color="textPrimary"
              variant="h5"
            >
              Delete
            </Typography>
            <Typography
              color="textSecondary"
              sx={{ mt: 1 }}
              variant="body2"
            >
              Are you sure you want to delete your project? All of
              your data will be permanently removed.
              This action cannot be undone.
            </Typography>
          </Box>
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'flex-end',
            px: 3,
            py: 1.5
          }}
        >
          <Button
            color="primary"
            sx={{ mr: 2 }}
            variant="outlined"
            onClick={handleClose2}
          >
            Cancel
          </Button>
          <Button
            sx={{
              backgroundColor: 'error.main',
              '&:hover': {
                backgroundColor: 'error.dark'
              }
            }}
            variant="contained"
            onClick = {deleteProject}
          >
            Delete
          </Button>
        </Box>
  </Box>
        </Dialog>
      </Box>
    </Card>
  );
};

ProjectCard.propTypes = {
  project: PropTypes.object.isRequired
};

export default ProjectCard;




