import PropTypes from 'prop-types';
import Markdown from 'react-markdown/with-html';
import { Box, Card, CardContent, Chip, Grid, Typography } from '@material-ui/core';
import { experimentalStyled } from '@material-ui/core/styles';

const MarkdownWrapper = experimentalStyled('div')(({ theme }) => ({
  color: theme.palette.text.primary,
  fontFamily: theme.typography.fontFamily,
  '& p': {
    marginBottom: theme.spacing(2)
  }
}));

const ProjectBrief = (props) => {
  const { description, tags, name, project, ...other } = props;
 let Description=project.description.substring(project.description.indexOf('>')+1,project.description.indexOf('</p>'));

  return (
    <Card  {...other}>
      <CardContent>
        <Grid
          container
          spacing={3}
        >
          <Grid
            item
            md={6}
            xs={12}
          >
            <Typography
              color="primary"
              variant="overline"
            >
              Project Name
            </Typography>
            <Typography
              color="textPrimary"
              variant="subtitle2"
            >
              {project.name}
            </Typography>
            <Box sx={{ mt: 4 }}>
             <Typography
              color="primary"
              variant="overline"
            >
              Project Phone Number
            </Typography>
            <Typography
              color="textPrimary"
              variant="subtitle2"
            >
              {project.projectPhone}
            </Typography>
            </Box>
            <Box sx={{ mt: 3 }}>
              <Typography
                color="primary"
                variant="overline"
              >
                Tags
              </Typography>
              <Box sx={{ mt: 1 }}>
              {Array.isArray(project.tags)?(
                project.tags.map((tag) => (
                  <Chip
                    key={tag}
                    label={tag}
                    variant="outlined"
                  />
                ))):( <Chip
                  key={tags}
                  label={tags}
                  variant="outlined"
                />)}
              </Box>
            </Box>
            
          </Grid>
        </Grid>
        <Box sx={{ mt: 3 }}>
          <Typography
            color="primary"
            sx={{ mb: 2 }}
            variant="overline"
          >
            Description
          </Typography>
         { project.description.includes("/")?(
          <Typography
              color="textPrimary"
              variant="subtitle2"
            >
             
              {project.description.substring(project.description.indexOf('>')+1,project.description.indexOf('</p>'))}
            </Typography>):(
                <Typography
                color="textPrimary"
                variant="subtitle2"
              >
               
                {project.description}
              </Typography>
            )}
          {/* <MarkdownWrapper>
            <Markdown source={project.description.substring(project.description.indexOf('>')+1,project.description.indexOf('</p>'))} />
          </MarkdownWrapper> */}
        </Box>
      </CardContent>
    </Card>
  );
};

ProjectBrief.propTypes = {
  description: PropTypes.string.isRequired,
  tags: PropTypes.arrayOf(PropTypes.string).isRequired,
  name: PropTypes.string.isRequired
};

export default ProjectBrief;
